.cont-page-about .cont-order__first .titleblue {
  color: rgba(var(--primary-color));
  font-size: 1.5rem;
  margin-bottom: 30px;
}
.cont-page-about .cont-order__first .formatted-text {
  color: rgba(var(--text-color));
  font-size: 1.2rem;
  margin-bottom: 15px;
}
.cont-page-about .cont-order__first .titleyellow {
  color: rgba(var(--primary-complement));
  font-size: 1.2rem;
  margin-bottom: 15px;
}

@media (min-width: 768px) {
  .cont-order {
    display: grid;
    grid-template-columns: 75% 25%;
  }
  .cont-order__first {
    order: 0;
  }
  .cont-order__first .titleblue {
    font-size: 2rem !important;
  }
  .cont-order__first .formatted-text {
    font-size: 1.7rem !important;
  }
  .cont-order__first .titleyellow {
    font-size: 1.7rem !important;
  }
  .cont-order__second {
    order: 2;
  }
  .cont-order__third {
    order: 1;
  }
}